* {
  margin: 0;
  padding: 0;
  bottom: 0;
  list-style-type: none;
}
.all-bloc {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: rgb(120, 203, 236);
  justify-content: space-around;
  align-items: center;
  form {
    width: 60%;
    border-radius: 5em;
    background: rgb(163, 202, 235);

    display: flex;

    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    height: 75%;
    div {
      width: 50%;
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 70px;
      justify-content: space-around;
      input {
        outline: none;
        border-radius: 10px;
        text-align: center;
        width: 50%;
        border: none;
        height: 2em;
      }
      input[type="number"]::-webkit-outer-spin-button,
      input[type="number"]::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }
      select {
        border-radius: 10px;
        text-align: center;
        height: 30px;
        width: 15%;
      }
     

     
    }
    button{
      border-radius: 10px;
      cursor: pointer;
      height: 35px;
    width: 75px;
    border: none;
    background: rgb(237, 242, 178);
    transition: all 0.2s ease-in-out;
    &:hover{
      background: rgb(166, 237, 118);
      scale: 1.2;
    }
    }
  }
  .user-list {
    margin-top: 25px;
    display: flex;
    flex-direction: column;
    background: rgb(37, 186, 240);
    width: 90%;

    justify-content: space-around;
    align-items: center;
  }
}
table {
  width: 100%;
  border-collapse: collapse;
}

th,
td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #f4f4f4;
}

tr:hover {
  background-color: #f1f1f1;
}
